import React, { Fragment } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import GitHubIcon from "@material-ui/icons/GitHub";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import InstagramIcon from "@material-ui/icons/Instagram";
import twitterIcon from "../../Assets/img/twitterSign.svg";
import googleIcon from "../../Assets/img/google.svg";

import fbIcon from "../../Assets/img/fbSign.svg";
import { googleSignin, fbSignin, twitterSignin } from "../backend/AxiosRequest";
import SocialButton from "./SocialButton";
import { updateAuthData } from "../redux/actions";
import { useDispatch, useTrackedState } from "reactive-react-redux";
import TwitterLogin from "react-twitter-login";
import Grid from "@material-ui/core/Grid";

//https://github.com/deepakaggarwal7/react-social-login/blob/master/demo/containers/demo.js

const handleSocialLoginFailure = (err) => {
  console.error(err);
};

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    flexDirection: "column",
    margin: theme.spacing(1),
  },
  button: {
    margin: "0.5rem",
  },
  googleStyle: {
    fillColor: theme.palette.primary.main,
  },
}));

export default function SocialSignin(props) {
  const classes = useStyles();
  const disapatch = useDispatch();
  const { authData } = useTrackedState();
  const [disable, setDisable] = React.useState(false)

  const handleFbLogin = (user) => {
    setDisable(true)
    console.log(user);
    fbSignin(user, authData, disapatch, updateAuthData);
  };

  const handleTwitterLogin = (err, user) => {
    setDisable(true)

    console.log(err, user);
    twitterSignin(user, authData, disapatch, updateAuthData);
  };

  const handleGoogleLogin = (user) => {
    setDisable(true)

    console.log(user);
    googleSignin(user, authData, disapatch, updateAuthData);
  };

  return (
    <div className={classes.root}>
      
      <SocialButton
        provider="facebook"
        appId="2005220039621593"
        onLoginSuccess={handleFbLogin}
        onLoginFailure={handleSocialLoginFailure}
        bttnStyle={props.bttnStyle}
        startIcon={<img src={fbIcon} style={{ height: "1.2rem" }} />}
        key={"fb"}
      >
        Sign in with Facebook
      </SocialButton>

      <SocialButton
        provider="google"
        appId="118066324373-tg599rfaeicsqgf2ubm6ova9jh18fhim.apps.googleusercontent.com"
        onLoginSuccess={handleGoogleLogin}
        onLoginFailure={handleSocialLoginFailure}
        bttnStyle={props.bttnStyle}
        startIcon={<img src={googleIcon} style={{ height: "1.2rem" }} />}
        key={"google"}
        scope={"https://www.googleapis.com/auth/userinfo.profile"}
      >
        Sign in with Google
      </SocialButton>

      <TwitterLogin
        authCallback={handleTwitterLogin}
        consumerKey={"gbpufedpU9Vw3gwS9e6MuFq6i"}
        consumerSecret={"j9MRpDO7NtNbs7gVqj08sqKyNwMRk6HQbhnZ5KoGCsNQEZyAoF"}
        callbackUrl={window.location.origin + "/sign-in"}
        children={
          <Button
            variant="contained"
            fullWidth
            // disabled={disable}
            color="secondary"
            className={props.bttnStyle}
            startIcon={<img src={twitterIcon} style={{ height: "1.6rem" }} />}
          >
            Sign in with Twitter
          </Button>
        }
      />
      <Grid item style={{marginTop:'1rem'}}>
     <p> No account? No problem.
        <Button style={{textTransform:'none', fontSize:"0.9rem", color:'#2D7DC1'}} size="small" onClick={props.signUp}>
              Sign up here
            </Button></p></Grid>
    </div>
  );
}
